          <div class="col" v-for="(item,index) in mone_money" :key="index">
            <div class="dash_card" :class="`bg${index+1}`" v-bind="[sums=sums+item.total_allocated_amount]">
                <div class="dash_text">{{item.title}}</div>
              <div class="dash_money">
                {{item.total_allocated_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}
              </div>
            </div>
          </div>
        </div>
        <div class="statistic_table">
          <div class="st_text large_text">Жами: {{sums.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")}}</div>
        </div>
