<script>
export default {
  components: { },
  data(){
    return{
      statistics:[],
      sums:0,
      statisticsId:[],
      qay_mab:0,
      xis_top:0,
      kassa:0,
      aj_mab:0,
      qoldiq:0,
      all_moneys:0,
      per_page:1,
      itId:0,
      statsExel:[],
      mone_money:[]
    }
  },
  mounted() {
this.sums=0
    this.$axiosDefault.get(`/stats-sport-types/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        })
        .then(stats=>{
          this.statistics=stats.data
          console.log(this.statistics,"jdnsajkdas")
          this.statistics.forEach(el=>{
            this.$axiosDefault.get(`/stats-sport-types/${el.id}/`,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                  }
                })
                .then(stats=>{
                  this.mone_money.push(stats.data)
                 
                })

          })
          this.$axiosDefault.get(`/stats-sport-types/${this.statistics[0].id}/sports?page=${this.per_page}`,
              {
                headers: {
                  'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
              })
              .then(stats=>{
                this.statisticsId=stats.data
                this.$axiosDefault.get(`/stats-sport-types/${this.statistics[0].id}/`,
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                      }
                    })
                    .then(stats=>{
                      this.$store.state.nav_title=stats.data.title
                      this.all_moneys=stats.data
                    })
                this.$axiosDefault.get(`/stats-sport-types/${this.statistics[0].id}/sports?page=${this.per_page}`,
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                      }
                    })
                    .then(stats=>{
                      this.statisticsId=stats.data
                      //start
                      this.$axiosDefault.get(`/stats-sport-types/${this.statistics[0].id}/sports?limit=1000`,
                          {
                            headers: {
                              'Authorization': `Bearer ${localStorage.getItem('token')}`
                            }
                          })
                          .then(stats=>{
                            console.log(stats.data.results)

                            stats.data.results.forEach(el=>{
                              this.statsExel.push({
                                "Спорт тури номи":el.title,
                                "Умумий ажратилган маблағ":el.balances[0].allocated_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                                "Ажратилган маблағ":el.balances[0].total_planned_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                                "Касса":el.balances[0].total_actual_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                                "Хисобот топширилди":el.balances[0].total_spent_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                                "Қайтарилган маблағлар":el.balances[0].total_returned_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                                "	Қолдиқ":el.balances[0].total_remained_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                              })
                              this.aj_mab=this.aj_mab+Number(el.balances[0].total_planned_amount)
                              this.kassa=this.kassa+Number(el.balances[0].total_actual_amount)
                              this.xis_top=this.xis_top+Number(el.balances[0].total_spent_amount)
                              this.qay_mab=this.qay_mab+Number(el.balances[0].total_returned_amount)
                              this.qoldiq=this.qoldiq+Number(el.balances[0].total_remained_amount)

                            })
                          })

                      //end
                    })
              })
        })
  },
  methods:{

    prev(){
      if(this.per_page>1){
        this.per_page--
        this.$axiosDefault.get(`/stats-sport-types/${this.itId}/sports?page=${this.per_page}`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(stats=> {
              this.statisticsId = stats.data
            })
      }
    },
    next(){
      if(this.per_page<this.statisticsId.total_pages){
        this.per_page++
        this.$axiosDefault.get(`/stats-sport-types/${this.itId}/sports?page=${this.per_page}`,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`
              }
            })
            .then(stats=> {
              this.statisticsId = stats.data
            })
      }
    },
    getReload() {
      this.$router.go()
    },
    getStatistic(it,index){

      this.qoldiq=0
      this.qay_mab=0
      this.xis_top=0
      this.kassa=0
      this.aj_mab=0
      let days = document.querySelectorAll('.button_menus')
      days.forEach(el=>el.classList.remove('active_m'))
      var element = document.getElementById(`statistic${index}`);
      element.classList.add("active_m");
      this.itId=it.id
      this.per_page=1
      this.$axiosDefault.get(`/stats-sport-types/`,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('token')}`
            }
          })
          .then(stats=>{
            this.statistics=stats.data

            this.$axiosDefault.get(`/stats-sport-types/${it.id}/sports?page=${this.per_page}`,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                  }
                })
                .then(stats=>{
                  this.statisticsId=stats.data
                  //start
                  this.$axiosDefault.get(`/stats-sport-types/${it.id}/sports?limit=1000`,
                      {
                        headers: {
                          'Authorization': `Bearer ${localStorage.getItem('token')}`
                        }
                      })
                      .then(stats=>{
                        this.statsExel=[]
                        stats.data.results.forEach(el=>{
                          this.statsExel.push({
                            "Спорт тури номи":el.title,
                            "Умумий ажратилган маблағ":el.balances[0].allocated_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                            "Ажратилган маблағ":el.balances[0].total_planned_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                            "Касса":el.balances[0].total_actual_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                            "Хисобот топширилди":el.balances[0].total_spent_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                            "Қайтарилган маблағлар":el.balances[0].total_returned_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                            "	Қолдиқ":el.balances[0].total_remained_amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                          })
                          this.aj_mab=this.aj_mab+Number(el.balances[0].total_planned_amount)
                          this.kassa=this.kassa+Number(el.balances[0].total_actual_amount)
                          this.xis_top=this.xis_top+Number(el.balances[0].total_spent_amount)
                          this.qay_mab=this.qay_mab+Number(el.balances[0].total_returned_amount)
                          this.qoldiq=this.qoldiq+Number(el.balances[0].total_remained_amount)

                        })
                      })

                  //end
                  this.$axiosDefault.get(`/stats-sport-types/${it.id}/`,
                      {
                        headers: {
                          'Authorization': `Bearer ${localStorage.getItem('token')}`
                        }
                      })
                      .then(stats=>{
                        this.$store.state.nav_title=stats.data.title
                        this.all_moneys=stats.data
                      })
                })
          })
    }
  }
}
</script>

